import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import Order from "../component/Order";
import ImageZooom from "react-image-zooom";

function Detail(props) {
  // product    .
  const { productId } = useParams();
  //shoesData    id    / 
  const product = props.filteredProducts.find((item) => item.id === parseInt(productId));

  //
  const [selectedSizes, setSelectedSizes] = useState([]);

  //       
  useEffect(() => {
    localStorage.setItem(
      "selectedProducts",
      JSON.stringify({
        id: product.id,
        name: product.name,
        src: product.src,
        price: product.price,
        color: product.color,
        style: product.style,
        size: selectedSizes,
      })
    );
  }, [selectedSizes, product]);

  function handleSizeChange(size) {
    //          
    if (selectedSizes.includes(size)) {
      //        
      setSelectedSizes(selectedSizes.filter((item) => item !== size));
    } else {
      //    
      setSelectedSizes([...selectedSizes, size]);
    }
  }

  //     
  const navigate = useNavigate();

  function arraysEqual(arr1, arr2) {
    //    
    if (arr1.length !== arr2.length) {
      return false;
    }

    //     
    for (let i = 0; i < arr1.length; i++) {
      if (arr1[i] !== arr2[i]) {
        return false;
      }
    }

    return true;
  }

  function handleClick() {
    const cartItems = JSON.parse(localStorage.getItem("cartItems")) || { selectedSizes: [], selectedProducts: [] };
    const existingProduct = cartItems.selectedProducts.find((item) => item.name === product.name && arraysEqual(item.size, selectedSizes));

    if (existingProduct) {
      //       
      existingProduct.quantity += 1;
      localStorage.setItem("cartItems", JSON.stringify(cartItems));
    } else {
      //    
      const newProduct = {
        id: product.id,
        name: product.name,
        src: product.src,
        price: product.price,
        color: product.color,
        style: product.style,
        size: selectedSizes,
        quantity: 1,
      };

      const updatedSelectedProducts = {
        selectedSizes: [...cartItems.selectedSizes, ...selectedSizes],
        selectedProducts: [...cartItems.selectedProducts, newProduct],
      };

      localStorage.setItem("cartItems", JSON.stringify(updatedSelectedProducts));
    }

    navigate("/cart");
  }
  if (!product) {
    return <div>    .</div>;
  }

  return (
    <div className="DetailPage">
      <div className="inner">
        <section className="product">
          <div className="product_img_wrapper">
            <ImageZooom src={`${process.env.PUBLIC_URL}/img/${product[`detailImg_${product.id}_1`]}`}></ImageZooom>
            <ImageZooom src={`${process.env.PUBLIC_URL}/img/${product[`detailImg_${product.id}_2`]}`}></ImageZooom>
            <ImageZooom src={`${process.env.PUBLIC_URL}/img/${product[`detailImg_${product.id}_3`]}`}></ImageZooom>
            <ImageZooom src={`${process.env.PUBLIC_URL}/img/${product[`detailImg_${product.id}_4`]}`}></ImageZooom>
          </div>
          <div className="recommend_shoes"></div>
        </section>
        <Order product={product} selectedSizes={selectedSizes} setSelectedSizes={setSelectedSizes} handleSizeChange={handleSizeChange} handleClick={handleClick} />
      </div>
    </div>
  );
}

export default Detail;
