import { useState, React } from "react";
import closthesData from "../Data/clothesData.js";
import shoesData from "../Data/shoesData.js";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Controller, EffectCreative } from "swiper";
// style
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/effect-creative";

function Jordan() {
  const [firstSwiper, setFirstSwiper] = useState(null);
  const [secondSwiper, setSecondSwiper] = useState(null);

  //      .
  let result = mergeArraysByJordan(shoesData, closthesData);
  // shoesData clothesData   
  function mergeArraysByJordan(shoesData, clothesData) {
    //     
    let result = [];

    // shoesData brand.jordan   push  
    shoesData.forEach((item) => {
      if (item.style === "jordan") {
        result.push(item);
      }
    });

    // clothesData brand.jordan   push  
    clothesData.forEach((item) => {
      if (item.style === "jordan") {
        result.push(item);
      }
    });

    // return  result  .
    return result;
  }

  function handleClick() {
    const imgWrapper = document.querySelector(".img_wrapper");
    const img = imgWrapper.querySelector("img");
    const video = document.createElement("video");
    document.querySelector(".img_wrapper_wrapper .btn_2").style.display = "none";
    video.src = process.env.PUBLIC_URL + "/img/jordan_video.mp4";
    video.autoplay = true;
    video.controls = true;
    imgWrapper.replaceChild(video, img);
  }

  return (
    <div className="inner">
      <section className="chance">
        <div className="img_wrapper_wrapper">
          <span className="btn_2" onClick={handleClick}>
             
          </span>
          <div className="img_wrapper">
            <img src={process.env.PUBLIC_URL + "/img/jordan.webp"} onClick={handleClick} />
          </div>
        </div>
        <div className="txt_wrapper">
          <h2>     </h2>
          <p className="desc">
                .       .
            <br />
                .
          </p>
          <ul>
            <button type="button" className="btn">
               
            </button>
            <button type="button" className="btn">
              
            </button>
          </ul>
        </div>
      </section>
      <section className="luka_zion">
        <div className="luka">
          <div className="img_wrapper">
            <img src={process.env.PUBLIC_URL + "/img/luka.jpg"}></img>
          </div>
          <div className="txt_wrapper">
            <span>'Mountains'</span>
            <h2> 1</h2>
            <p className="desc">
                       .
              <br />
                   1 'Mountains' 
              <br />
              IsoPlate   23       .
              <br />
               1     .
            </p>
            <button type="button" className="btn">
              
            </button>
          </div>
        </div>
        <div className="zion">
          <div className="img_wrapper">
            <img src={process.env.PUBLIC_URL + "/img/zion.jpg"}></img>
          </div>
          <div className="txt_wrapper">
            <span>'Spring Hoops'</span>
            <h2> 2</h2>
            <p className="desc">
                (EYBL)       2  '    .
              <br />
                             
              <br />
                   .
            </p>
            <button type="button" className="btn">
              
            </button>
          </div>
        </div>
      </section>
      <section className="flight">
        <h2 className="">Now In Flight</h2>
        <div className="top_slide">
          <Swiper
            modules={[Navigation, Controller, EffectCreative]}
            effect={"creative"}
            onSwiper={firstSwiper}
            controller={{ control: secondSwiper }}
            loop={true}
            creativeEffect={{ prev: { shadow: true, translate: [0, 0, -400] }, next: { translate: ["100%", 0, 0] } }}
          >
            {result.map(function (a, i) {
              return (
                <SwiperSlide key={i}>
                  <div className="img_wrapper">
                    <img src={process.env.PUBLIC_URL + "/img/" + result[i].src}></img>
                  </div>
                  <div className="txt_wrapper">
                    <h2>{result[i].name}</h2>
                    <span className="category">{result[i].category}</span>
                    <p className="desc">{result[i].desc}</p>
                    <button type="button" className="btn">
                       
                    </button>
                  </div>
                </SwiperSlide>
              );
            })}
          </Swiper>
        </div>
      </section>
      <section className="jordan_row">
        <div className="img_wrapper">
          <img src={process.env.PUBLIC_URL + "/img/jordan_low.webp"}></img>
        </div>
        <div className="txt_wrapper">
          <span>'Stunt'</span>
          <h2>  37 </h2>
          <p className="desc">
              37  'Stunt'     
            <br />
                .{" "}
          </p>
          <button type="button" className="btn">
            
          </button>
        </div>
      </section>
      <section className="not_6">
        <div className="img_wrapper">
          <img src={process.env.PUBLIC_URL + "/img/why_6.webp"}></img>
        </div>
        <div className="txt_wrapper">
          <span>'Spirit'</span>
          <h2> WHY NOT.6</h2>
          <p>
              Why Not. 6           .
            <br />
               ,   ,        .
          </p>
          <button type="button" className="btn">
            
          </button>
        </div>
      </section>
      <section className="special_jordan">
        <h2>Special Jordan</h2>
        <div className="special_wrapper">
          <div className="air_wrapper">
            <div className="air">
              <h3>  1 </h3>
              <button type="button" className="btn">
                
              </button>
            </div>
          </div>
          <div className="paris_wrapper">
            <div className="paris">
              <h3> X  </h3>
              <button type="button" className="btn_2">
                
              </button>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Jordan;
