import { useState } from "react";
import bestData from "../Data/bestData.js";
import shoesData from "../Data/shoesData.js";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";
import SlideItems from "../component/Slideitems.js";
import { Link } from "react-router-dom";
// style
import "swiper/css";
import "swiper/css/navigation";

console.timeLog(shoesData);

function MainPage() {
  let [Shoes, setShoes] = useState(["yoga", "training", "running"]);
  let [ShoesTitle, setShoesTitle] = useState([" (Yoga)", " (Training)", " (Running)"]);
  let [imgSrc, setImgSrc] = useState(process.env.PUBLIC_URL + "/img/nike_leggings_model.jpg");
  const imgOver = () => {
    setImgSrc(process.env.PUBLIC_URL + "/img/nike_leggings_model_2.jpg");
  };
  const imgOut = () => {
    setImgSrc(process.env.PUBLIC_URL + "/img/nike_leggings_model.jpg");
  };

  return (
    <div className="inner">
      <section className="special_nike">
        <div className="video_wrapper">
          <video src={process.env.PUBLIC_URL + "/img/main_video.mp4"} loop={true} autoPlay={true} muted={true}></video>
        </div>
        <div className="txt_wrapper">
          <h2>   </h2>
          <p className="desc">
               ,    ,    .
            <br />
                 .
          </p>
          <div className="img_wrapper">
            <img src={process.env.PUBLIC_URL + "/img/qr_code.jpg"}></img>
          </div>
          <p> </p>
        </div>
      </section>
      <section className="leggings">
        <div className="leggings__txt_wrapper">
          <span>'Anytime, Anywhere'</span>
          <h2>    </h2>
          <div>
            <p className="desc">            .</p>
            <ul className="btn_wrapper">
              <li>
                <button type="button" className="btn">
                   
                </button>
              </li>
              <li>
                <button type="button" className="btn">
                  
                </button>
              </li>
            </ul>
          </div>
        </div>
        <div className="img_wrapper">
          <img onMouseOver={imgOver} onMouseOut={imgOut} src={imgSrc}></img>
        </div>
      </section>
      <section className="leggings_list">
        <h3>Everyday Leggings</h3>
        <div className="list">
          {Shoes.map(function (a, i) {
            return (
              <div className="list_bg_wrapper">
                <div className={Shoes[i]} key={i}>
                  <h4>{ShoesTitle[i]}</h4>
                  <button type="button" className="btn_2">
                    
                  </button>
                </div>
              </div>
            );
          })}
        </div>
      </section>
      <section className="running_guide">
        <div className="video_wrapper">
          <video src={process.env.PUBLIC_URL + "/img/running.mp4"} loop={true} autoPlay={true} muted={true}></video>
        </div>
        <div className="txt_wrapper">
          <h2>  </h2>
          <p className="desc">   ,      .</p>
          <button type="button" className="btn">
             
          </button>
        </div>
      </section>
      <section className="featured">
        <h3>Featured Shoes</h3>
        <SlideItems />
      </section>
      <section className="new_challenge">
        <div className="video_wrapper">
          <video src={process.env.PUBLIC_URL + "/img/new_challenge.mp4"} loop={true} autoPlay={true} muted={true}></video>
        </div>
        <div className="txt_wrapper">
          <h2> </h2>
          <p className="desc">
               ,   .
            <br />
             ,         .
          </p>
          <button type="button" className="btn">
             
          </button>
        </div>
      </section>
      <section className="best">
        <div className="title_wrapper">
          <h2></h2>
          <p className="h2_desc">   .</p>
        </div>
        {bestData.map(function (a, i) {
          return (
            <div className="best_wrapper" key={i}>
              <Link to={bestData[i].Link}>
                <div className="img_wrapper">
                  <img src={process.env.PUBLIC_URL + "/img/" + bestData[i].src}></img>
                </div>
                <div className="txt_wrapper">
                  <div>
                    <h3>{bestData[i].name}</h3>
                    <p className="category">{bestData[i].category}</p>
                    <p className="price">{bestData[i].price}</p>
                  </div>
                  <div>
                    <p className="desc">{bestData[i].desc}</p>
                    <button type="button" className="btn">
                      
                    </button>
                  </div>
                </div>
              </Link>
            </div>
          );
        })}
      </section>
      <section className="nike_series">
        <div className="nike_app">
          <a href="#">
            <div className="img_wrapper">
              <img src={process.env.PUBLIC_URL + "/img/nike_app.jpg"}></img>
            </div>
            <h2> </h2>
            <p className="desc">   ,    ,    ,      .</p>
            <button type="button" className="btn">
               
            </button>
          </a>
        </div>
        <div className="NTC">
          <a href="#">
            <div className="img_wrapper">
              <img src={process.env.PUBLIC_URL + "/img/ntc.jpg"}></img>
            </div>
            <h2>  (NTC)</h2>
            <p className="desc">
              NTC       .
              <br />
              , , ,         .
            </p>
            <button type="button" className="btn">
               
            </button>
          </a>
        </div>
        <div className="NRC">
          <a href="#">
            <div className="img_wrapper">
              <img src={process.env.PUBLIC_URL + "/img/nrc.jpg"}></img>
            </div>
            <h2>  (NRC)</h2>
            <p className="desc">
              NRC       .
              <br />
                         .
            </p>
            <button type="button" className="btn">
               
            </button>
          </a>
        </div>
      </section>
    </div>
  );
}

export default MainPage;
